@import "../../variables";

@media (max-width: $media-phone-max) {

  #ladder-holder {
    #ladder-root {
      display: none;
      height: 554px;
      width: 320px;
      border-radius: 10px;
      #ladder-inner {
        #score-ladder {
          margin-top: 50px;
          width: 100%;
          font-size: 1.2em;
          line-height: 1.4em;
          tr {
            height: 24px;
            td {
              font-size: 14px;
              div.player-score {
                height: 24px;
                .username {
                  right: 45px;
                }
                .tournament-chip {
                  width: 40px;
                  height: 20px;
                }
              }
              div.other-player-score {
                height: 30px;
                .username {
                  left: 45px;
                }
                .tournament-chip {
                  width: 40px;
                  height: 20px;
                }
              }
              .size-xlarge {
                width: 150px;
                .username {
                  max-width: 115px;
                }
              }
              .size-large {
                width: 145px;
                .username {
                  max-width: 110px;
                }
              }
              .size-medium {
                width: 140px;
                .username {
                  max-width: 105px;
                }
              }
              .size-small {
                width: 135px;
                .username {
                  max-width: 85px;
                }
              }
              .size-xsmall {
                width: 130px;
                .username {
                  max-width: 80px;
                }
              }
            }
          }
          &.score-size-64 {
            margin-top: 0px;
            tr.normal {
              height: 80px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 80px;
                  .tournament-chip {
                    top: 30px;
                  }
                  .username {
                    top: 28px;
                  }
                }
              }
            }
          }
          &.score-size-128 {
            tr.normal {
              height: 62px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 62px;
                  .tournament-chip {
                    top: 23px;
                  }
                  .username {
                    top: 20px;
                  }
                }
              }
            }
          }
          &.score-size-256 {
            margin-top: 0px;
            tr.normal {
              height: 60px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 60px;
                  .tournament-chip {
                    top: 20px;
                  }
                  .username {
                    top: 19px;
                  }
                }
              }
            }
          }
          &.score-size-512 {
            tr.normal {
              height: 50px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 50px;
                  .tournament-chip {
                    top: 17px;
                  }
                  .username {
                    top: 15px;
                  }
                }
              }
            }
          }
          &.score-size-1024 {
            tr.normal {
              height: 45px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 45px;
                  .tournament-chip {
                    top: 12px;
                  }
                  .username {
                    top: 10px;
                  }
                }
              }
            }
          }
          &.score-size-2048 {
            margin-top: 10px;
            tr.normal {
              height: 44px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 44px;
                  .tournament-chip {
                    top: 12px;
                  }
                  .username {
                    top: 10px;
                  }
                }
              }
            }
          }
          &.score-size-4096 {
            margin-top: 0px;
            tr.normal {
              height: 42px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 42px;
                  .tournament-chip {
                    top: 10px;
                  }
                  .username {
                    top: 10px;
                  }
                }
              }
            }
          }
          &.score-size-8192 {
            margin-top: 0px;
            tr.normal {
              height: 39px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 39px;
                  .tournament-chip {
                    top: 10px;
                  }
                  .username {
                    top: 8px;
                  }
                }
              }
            }
          }
          &.score-size-16384 {
            margin-top: 4px;
            tr.normal {
              height: 36px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 36px;
                  .tournament-chip {
                    top: 8px;
                  }
                  .username {
                    top: 6px;
                  }
                }
              }
            }
          }
          &.score-size-131072 {
            margin-top: 5px;
            tr.normal {
              height: 30px;
              td {
                font-size: 16px;
                div.player-score, div.other-player-score {
                  height: 30px;
                  .tournament-chip {
                    top: 5px;
                  }
                  .username {
                    top: 4px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

